.navbar {
  width:100%;
  height: 70px;
  background-color: darkslategrey;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1rem;
  z-index: 50;

}

.navbar-logo h2{
   color: #f4f4f4;
   font-size: 1.4rem;
   cursor: pointer;
}

.navbar-links{
    display: flex;
    list-style: none;
    align-items: center;
}

.navbar-links > li{
    padding-left: 1.5rem;
}

.navbar-links >li > a{
    text-decoration: none;
    color: #f4f4f4;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
}

.navbar-links >li > a > span{
    display: flex;
    align-items: center;
    margin-left: 8px;
}

.cart-link{
    background-color: #333;
    padding: 10px;
    border-radius: 8px;
}

.cart-link:hover{
    color: white;
    background: darkmagenta;
}

.cartlogo-badge{
    width: 30px;
    height: 30px;
    background: #f4f4f4;
    border-radius: 20%;
    margin-left: 8px;
    color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
}

.hamburger-menu{
    width: 30px;
    height: 30px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.hamburger-menu > div{
    width:100%;
    height: 3px;
    background: #f4f4f4;
}

.hamburger-menu:hover > div{
  background: purple;
}

@media (max-width: 700px) {
    .navbar-links {
      display: none ;
    }
  
    .hamburger-menu {
      display: flex ;
    }
  }
  
  @media (max-width: 500px) {
    .navbar-logo h2 {
      font-size: 1rem;
    }
  }