
  
.homescreen {
    max-width: 1300px;
    margin: 1rem auto;
    height: 60%;
  }
  
  .homescreen-title {
    font-size: 1.5rem;
    color: #171717;
    height: 20px;
    margin-bottom: 1rem;
    margin-left: 8px;
  }
  
  .homescreen-products {
    
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  @media (max-width: 1232px) {
    .homescreen-products {
      grid-template-columns: repeat(3, auto);
    }
  }
  
  @media (max-width: 950px) {
    .homescreen-products {
      grid-template-columns: repeat(2, auto);
    }
  }
  
  @media (max-width: 630px) {
    .homescreen-products {
      grid-template-columns: 1fr;
    }
  }