.cartscreen{
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;

}

.cartscreen h2{
    margin-bottom: 1rem;
}

.cartscreen-left{
  flex: 0.7;
  margin-right: 1rem;
  background-color: transparent;
  padding: 1rem;

}

.cartscreen-right{
    flex: 0.3;
    background: white;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
    /* margin: 15px; */

}

.cartscreen-right div{
    border-bottom: 1.5px solid rgba(0,0,0, 0.2);
    padding: 1rem;
}

.cartscreen-right div:last-child{
    border: none;
}

.cartscreen-info p{
    padding: 8px;
}

.cartscreen-right div button {
    padding: 10px 17px;
    width: 100%;
    background: black;
    color: white;
    border: 1px solid black;
    cursor: pointer;

}

.cartscreen-right div button:hover {
  opacity: 0.85;
}

@media (max-width: 1320px) {
    .cartscreen {
      max-width: 900px;
    }
  }
  
  @media (max-width: 960px) {
    .cartscreen {
      max-width: 800px;
    }
  }
  
  @media (max-width: 960px) {
    .cartscreen {
      flex-direction: column;
    }
  
    .cartscreen__right {
      margin: 1rem;
    }
  }